<template>
  <div id="start">

    <div class="start-header">
      <img src="../assets/images/canju.png" alt="餐具">
        <span>用餐人数</span>
    </div>
    <p class="context">请选择正确用餐人数，小二马上给您安排</p>
    <div class="people-num">
      <ul>
        <li class="active"><span>1人</span></li>
        <li><span>2人</span></li>
        <li><span>3人</span></li>
        <li><span>4人</span></li>
        <li><span>5人</span></li>
        <li><span>6人</span></li>
        <li><span>7人</span></li>
        <li><span>8人</span></li>
        <li><span>9人</span></li>
        <li><span>10人</span></li>
        <li><span>11人</span></li>
        <li><span>12人</span></li>
      </ul>
    </div>
    <div class="mask"><input type="text" v-model="mask_input" placeholder="请输入味道偏好及偏口（可选填）"></div>
    <ul class="mask_list">
        <li class="active"><span>不辣</span></li>
        <li><span>微辣</span></li>
        <li><span>重辣</span></li>
    </ul>

    
    <div class="bottom-menu" @click="pushData()">
      <span>
        开始点单
      </span>
    </div>
   
  
  </div>
</template>

<script>
import '../assets/css/start.css'
import config from "../assets/model/config.js"
export default {
  name: 'app',
  data () {
    return {
      mask_input:"",
      p_num:"1人",
      api:config.api,

     
    }
  },
  methods:{
    pnumChange(){
      var that=this;
      var pnum=document.querySelectorAll(".people-num li");
     // console.log(pnum);
      
      for (var i=0; i<pnum.length; i++) {
        pnum[i].onclick=function() {
          for (var j =0; j<pnum.length;  j++) {
            pnum[j].className="";
          }
          this.className="active";

          that.p_num=this.querySelector("span").innerHTML;
         
        }

       
      }


      var masklistDom=document.querySelectorAll(".mask_list li");

      for (var i=0; i<masklistDom.length; i++) {
        masklistDom[i].onclick=function() {
          for (var j =0; j<masklistDom.length;  j++) {
            masklistDom[j].className="";
          }
          this.className="active";
          that.mask_input=this.querySelector("span").innerHTML;

          
        }

       
      }

    },
    pushData(){
      var api=this.api+'api/addPeopleInfo';
      this.$http.post(api,{
        uid:'a002',
        p_num:this.p_num,
        p_mark:this.mask_input,


      }).then((res)=>{
        console.log(res);
        if(res.body.success){
          this.$router.push({path:"/index"});
        }
      },(err)=>{
        console.log(err);
      });
    }
  

  },mounted(){
    this.pnumChange();
    
   }
}
  

</script>

<style lang="scss">


</style>



