<template>
  <div id="cart">
  	<div id="num" class="num">
			<h3>购物车</h3>
			<div class="pnum">
			    <div class="pnum_left">
			    	<p>用餐人数：<span>{{peopleinfo.p_num}}</span></p>
			        <p>备注：<span v-if="!peopleinfo.p_mark">无</span><span v-if="peopleinfo.p_mark">{{peopleinfo.p_mark}}</span></p>
			    </div>
			    <div class="pnum_right">
			    	<router-link to="/edit">
			    	<img src="../assets/images/edit.png" alt="">
			        <p>修改</p>
			        </router-link>
			    </div>
			</div>
			<div class="food_num">
				<p>购物车中总共有{{getTotal_num}}个菜</p>
				<p>合计：<span>￥{{getTotal_price}}</span></p>
			</div>

		</div>
		<div id="nothing" class="nothing" v-if="!getTotal_num">
                <h3>购物车空空的</h3>
                <p>请点击下面的菜单继续点单</p>
        </div>

		<div id="food_list" class="food_list" v-for="(item,key) in list">
			<ul>
				<li class="item_list">
					<div class="food_left">
						<img :src="api+item.img_url" alt="">
						<div class="food_info">
						    <p>{{item.title}}</p>
						    <p>￥{{item.price}}</p>
						</div>
			     	</div>
			     	<div class="cart_right">
			     		<div class="input_left" @click="decNum(item,key)">-</div>
			     		<div class="input_center"><input type="text" v-model="item.num"></div>
			     		<div class="input_right" @click="incNum(item)">+</div>
			     	</div>

				</li>
			</ul>
			
		</div>
		<div id="hot_food" class="hot_food">
			<h3>本店顾客最常点的菜品</h3>
			<div class="content_list">
		        <ul class="item_list">
                       <li>
                             <div class="inner">
                             <img src="../assets/images/1.png" alt="">
                             <p class="pname">湛江白切鸡</p>
                             <p class="price">￥26</p>
                             </div>
                       </li>
                       <li>
                             <div class="inner">
                             <img src="../assets/images/2.png" alt="">
                             <p class="pname">潮汕辣子鸡</p>
                             <p class="price">￥26</p>
                             </div>
                       </li>
                       <li>
                             <div class="inner">
                             <img src="../assets/images/3.png" alt="">
                             <p class="pname">雷州烧生蚝</p>
                             <p class="price">￥26</p>
                             </div>
                       </li>
                       <li>
                             <div class="inner">
                             <img src="../assets/images/4.png" alt="">
                             <p class="pname">贵州黄焖鸡</p>
                             <p class="price">￥26</p>
                             </div>
                       </li>
                       <li>
                             <div class="inner">
                             <img src="../assets/images/5.png" alt="">
                             <p class="pname">佛山大杂烩</p>
                            <p class="price">￥26</p>
                            </div>
                         </li>
                         <li>
                             <div class="inner">
                             <img src="../assets/images/6.png" alt="">
                             <p class="pname">杭州东坡肉</p>
                             <p class="price">￥26</p>
                             </div>
                       </li>
                       <li>
                             <div class="inner">
                             <img src="../assets/images/7.png" alt="">
                             <p class="pname">湛江白切鸡</p>
                             <p class="price">￥26</p>
                             </div>
                       </li>
                       
                       <li>
                             <div class="inner">
                             <img src="../assets/images/2.png" alt="">
                             <p class="pname">湛江白切鸡</p>
                             <p class="price">￥26</p>
                             </div>
                       </li>
          
                </ul>

			</div>
	
		</div>
		<v-nav></v-nav>
		<div id="footer_book" class="footer_book">
			<router-link to="/index">
			<img src="../assets/images/menu.png"/>
			<p>菜单</p>
			</router-link>
		</div>
		
		<div id="footer_cart" class="footer_cart">
			<img src="../assets/images/doorder.png"/>
			<p>下单</p>
		</div>

  </div>

</template>

<script>
import nav from "./public/nav.vue"
import config from "../assets/model/config.js"
export default {
 
  data () {
    return {
      msg: '',
      list:[],
      api:config.api,
      peopleinfo:"1人",
      getTotal_price:0,
      getTotal_num:0,
    }
  },
  methods:{
  	requestCartlist(){
  		var api=this.api+'api/cartlist?uid=a002';
  		this.$http.jsonp(api).then((res)=>{
  			console.log(res);
  			this.list=res.body.result;
  			this.getTotal();
  			

  		},(err)=>{
  			console.log(err);

  		});

  	},
  	incNum(item){
  		var product_id=item.product_id;
  		var num=item.num;
  		var api=this.api+'api/incCart?uid=a002&product_id='+product_id+'&num='+num;
  		this.$http.jsonp(api).then((res)=>{
  			console.log(res);
  			this.getTotal();


  		},(err)=>{
  			console.log(err);

  		});
  		++item.num;

  	},
  	decNum(item,key){
  		var product_id=item.product_id;
  		var num=item.num;
  		var api=this.api+'api/decCart?uid=a002&product_id='+product_id+'&num='+num;
  		this.$http.jsonp(api).then((res)=>{
  			console.log(res);
  			this.getTotal();

  		},(err)=>{
  			console.log(err);

  		});
  		if(item.num==1){
  			this.list.splice(key,1);

  			
  		}else{
  			--item.num;


  		}
  		

  	},
  	getpeopleinfo(){
  		var api=this.api+'api/peopleInfoList?uid=a002';
  		this.$http.jsonp(api).then((res)=>{
  			console.log(res);
  			this.peopleinfo=res.body.result[0];
  		
  		},(err)=>{
  			console.log(err);

  		});

  	},

  	//计算总价格及菜品数量
  	getTotal(){

        var getTotal_price=0;
        var getTotal_num=0;
  	 	for(var i=0;i<this.list.length;i++){
  				getTotal_price +=parseFloat(this.list[i].price* this.list[i].num);
  				getTotal_num +=parseInt(this.list[i].num);
  			
  			}
  		this.getTotal_num=getTotal_num;
  		this.getTotal_price=getTotal_price;
  	},
  

  },
  mounted(){
  	this.requestCartlist();
  	this.getpeopleinfo();
  	
  },
  components:{
  	"v-nav":nav,
  }
 
  }
  

</script>
<style lang="scss">
@charset "UTF-8";
.num{
	margin-top:1.6rem;
	background:#fff;
	border-radius: .8rem;
	
	h3{
		text-align:center;
		padding:1.2rem;
		font-size:2rem;
		border-bottom: 1px solid #eee;
	}
	.pnum{
		display: flex;
		padding: .8rem 0.3rem;
		border-bottom: 1px solid #eee;
		.pnum_left{
			flex:1;
			p{
				padding:.2rem 0;
				&:first-child
				{

					color:red;
			     }
			}
			
		}
		.pnum_right{
			margin-right: .8rem;
			img{
				width:2rem;
				height: 2rem;
			}
			p{
				text-align: center;
			}
		}

	}
	.food_num{
		padding: .8rem 0.3rem;
		p{
			span{
				color: red;
				font-size: 2.2rem;

			}
		}

	}

}


.food_list{
	background: #fff;
	margin-top: 1.2rem;
	border-radius: .8rem;

	li{
		display: flex;
		padding: 1rem;
		border-bottom: 1px solid #eee;
		.food_left{

			display: flex;
			flex:2;

			img{
				width: 4rem;
				height:4rem;
			}
			.food_info{
				flex:1;
				padding: 0 .8rem;


			}
		}
		.cart_right{
			display: flex;

			.input_left,.input_right{
				font-size: 2.4rem;
				width:3rem;
				height: 3rem;
				//box-sizing:border-box;
				border:1px solid #eee;
				color: red;
				text-align: center;
				line-height: 3rem;
				//padding-top: -2rem ;
			}
			.input_center{


				input{
					width: 3rem;
					height: 3rem;
					border:1px solid #eee;
					text-align: center;
				}
				
			}
		}

	}

}

.hot_food{
	background: #fff;
	margin-top: 1rem;
	border-radius: .8rem;
	h3{
		
		padding: 1rem 0;
	}
	.content_list{
		width: 100%;
		height: 12rem;
		overflow-x: auto;
		margin-left: .4rem;

		ul{
			display:flex;


			
			li{
				flex:1;
				margin-left: .6rem;
				
				.inner{
					border-radius: .8rem;
					overflow: hidden;
					
					img{
						width:7rem;
						height: 7rem;
						
					}
				}
			}

		}

	}
}

// 空的购物车
.nothing{
	text-align: center;
	margin-top: 4.6rem;

	h3{
		font-size: 1.8rem;
		padding: 2rem;

	}
}

</style>


