<template>
  <div id="home">
    <div id="order_info" class="order_info">
        <div id="order_top" class="order_top">
          <img src="../assets/images/timer.png" alt="">
          <div class="order_right">
            <p>16号桌待门店接单</p>
            <p>请及时联系服务员确认菜品信息!</p>
          </div>
        </div>
        <h3>已点菜品5份，合计：<span>￥156元</span></h3>
    </div>
    <div id="food_list" class="food_list">
      <h3>菜品详情:</h3>
      <div class="order_list">
        <ul>
          <li>
            
                <div class="title">
                  烧茄子
                </div>
                  
                <div class="num">
                  
                  1份
                </div>
                
                
                <div class="price">
                  
                  12元
                </div>
          </li>
          <li>
            
                <div class="title">
                  炒辣椒
                </div>
                  
                <div class="num">
                  
                  1份
                </div>
                
                
                <div class="price">
                  
                  12元
                </div>
          </li>
          <li>
            
                <div class="title">
                  闷烧鸡
                </div>
                  
                <div class="num">
                  
                  1份
                </div>
                
                
                <div class="price">
                  
                  12元
                </div>
          </li>
        </ul>
        
      </div>
    </div>
  </div>
</template>

<script>
export default {
 
  data () {
    return {
      msg: '',
      list:[]
    }
  },
  methods:{
   

  },
 
  }
  

</script>

<style lang="scss">
@charset "UTF-8";
.order_info{
  margin:1.2rem;
  background: #fff;
  border-radius: .8rem;

  .order_top{
    display: flex;
    padding:2rem;
      img{
    width:5.2rem;
    height:5.2rem;

     }
     .order_right{

      p{
        &:first-child{
          font-size:2rem;
        }
      }

  }

  }
  h3{
    padding:2rem;
    span{
      color:red;
      font-size:2rem;
    }
  }
  
}

.food_list{
  background: #fff;
  border-radius: .8rem;
    margin:0 1.2rem;


  h3{
    padding:.8rem 0 .8rem .8rem;

    


  }
  .order_list{
    
    li{
      display: flex;
      padding: 1.2rem;
      .title{
        flex:2;
      }
      .num,.price{
        flex:1;
        text-align: center;
      }
    }
  }

}


// 付款页面
.orderpay{
  margin:1.2rem;
  background: #fff;
  border-radius: .8rem;
  h3{
    text-align: center;
    font-size: 2rem;
    padding: 1rem;
  }

  .pay_top{
    display: flex;
    padding: 1rem;
    
    border-bottom: 1px solid #eee;
    div{
      flex:1;
      padding-bottom: .5rem;

    }


  }
  .pay_bottom{
    display: flex;
    padding:1.4rem 1rem;

    .pay_list{
      flex:1;
      span{
        color:red;
        font-size: 2rem;
      }
  

    }
    .pay_button{

      width:8rem;
      height:3.2rem;
      background: red;
      border-radius: .5rem;
      text-align: center;
      color: #fff;
      line-height: 3.2rem;
      margin-right: .8rem;

    

    }
  }
  
}

</style>



