<template>
  <div id="edit">

    <div class="start-header">
      <img src="../assets/images/edit.png" alt="餐具">
        <span>修改人数</span>
    </div>
    <p class="context">请修改正确的用餐人数，并确定最终修改</p>
    <div class="people-num">
      <ul>
        <li  v-for="(item,key) in num_list" :class="{'active':parseInt(peopleinfo.p_num)==key+1}"><span>{{item}}人</span></li>

      </ul>
    </div>
    <div class="mask"><input type="text" v-model="mask_input" placeholder="请输入味道偏好及偏口（可选填）"></div>
    <ul class="mask_list">
        <li class="active"><span>不辣</span></li>
        <li><span>微辣</span></li>
        <li><span>重辣</span></li>
    </ul>

    
    <div class="editmenu-cancel">
      <router-link to="/cart">
         <span>
          取消
         </span>
      </router-link>
    </div>
    <div class="editmenu-confirm" @click="pushData()">
      <span>
        确定修改
      </span>
    </div>
   
  
  </div>
</template>

<script>
import '../assets/css/start.css'
import config from "../assets/model/config.js"
export default {
  name: 'app',
  data () {
    return {
      mask_input:"",
      p_num:"1人",
      api:config.api,
      num_list:[],
      peopleinfo:[],

     
    }
  },
  methods:{
    pnumChange(){
      var that=this;
      var pnum=document.querySelectorAll(".people-num li");
     // console.log(pnum);
      
      for (var i=0; i<pnum.length; i++) {
        pnum[i].onclick=function() {
          for (var j =0; j<pnum.length;  j++) {
            pnum[j].className="";
          }
          this.className="active";

          that.p_num=this.querySelector("span").innerHTML;
         
        }

       
      }


      var masklistDom=document.querySelectorAll(".mask_list li");

      for (var i=0; i<masklistDom.length; i++) {
        masklistDom[i].onclick=function() {
          for (var j =0; j<masklistDom.length;  j++) {
            masklistDom[j].className="";
          }
          this.className="active";
          that.mask_input=this.querySelector("span").innerHTML;

          
        }

       
      }

    },
    pnum_list(){
      for (var i = 0; i < 12; i++) {
        this.num_list.push(i+1);
        
      }

    },
      getpeopleinfo(){
      var api=this.api+'api/peopleInfoList?uid=a002';
      this.$http.jsonp(api).then((res)=>{
        console.log(res);
        this.peopleinfo=res.body.result[0];
        this.mask_input=this.peopleinfo.p_mark;
      
      },(err)=>{
        console.log(err);

      });

    },
    pushData(){
      var api=this.api+'api/addPeopleInfo';
      this.$http.post(api,{
        uid:'a002',
        p_num:this.p_num,
        p_mark:this.mask_input,


      }).then((res)=>{
        console.log(res);
        if(res.body.success){
          this.$router.push({path:"/cart"});
        }
      },(err)=>{
        console.log(err);
      });
    }
  

  },mounted(){
   
    this.pnum_list();
    this.getpeopleinfo();
    this.$nextTick(function(){
      this.pnumChange();

    });
    
   }
}
  

</script>

<style lang="scss">



</style>



