<template>
  <div id="index">
    <header id="index_header" class="index_header">
    	    
			<div class="hlist">
				<router-link to="hot">
				<img src="../assets/images/rexiao.png" alt="热销">
				<p>热销榜</p>
				</router-link>
			</div>
		   

			<div class="hlist">
				<router-link to="cart">
				<img src="../assets/images/caidan.png" alt="菜单">
				<p>点过的菜</p>
                </router-link>
			</div>
			<div class="hlist">
				<router-link to="search">
				<img src="../assets/images/sousuo.png" alt="搜索">
				<p>搜你喜欢</p>
			    </router-link>
			</div>

		</header>
		<aside class="left_menu" id="left_menu">
			<ul>
				<li v-for="(item,key) in list" @click="scroll_top(key)">{{item.title}}</li>
					
			
			</ul>
			<div class="nav_img" id="nav_img">
				<img src="../assets/images/nav.png" alt="">
				<p>菜单</p>
			</div>
		</aside>
		<div class="content" v-for="item in list">
			<h3 class="content_title">{{item.title}}</h3>
			<div class="content_list">
				<ul class="item_list">
					<li v-for="food in item.list">
						<router-link :to="'/pcontent?id='+food._id">
                        <div class="inner">
				        <img :src="api+food.img_url" alt="">
				        <p class="pname">{{food.title}}</p>
				        <p class="price">￥{{food.price}}</p>
				        </div>
				        </router-link>
					</li>
				
					
				</ul>

			</div>
			

		</div>

		<div class="bg" id="bg">
			
			
		</div>
		
		<v-nav></v-nav>
		<div id="footer_cart" class="footer_cart">
			<router-link to="/cart">
		  <img src="../assets/images/cart.png"/>
		  <p>购物车</p>
		  <span class="cartnum" >{{cartnum}}</span>
		  </router-link>
		</div>

  </div>
</template>

<script>
import config from "../assets/model/config.js"

import nav from "./public/nav.vue"

		 		  
export default {
  name: 'app',
  data () {
    return {
    	list:[],
    	api:config.api,
    	cartnum:1
    
    }
  },
  methods:{
  	menuclick(){

  		  
			  		
			  		//按钮
			  		var btn=document.getElementById('nav_img');
			  		//分类
			  		var leftmenu=document.getElementById('left_menu');
			  		
			  		//背景			  		
			  		var bg=document.getElementById('bg');			  		
			  		
			  		var flag=true;
			  		
			  		bg.onclick=btn.onclick=function(){
			  			
			  			if(flag){
			  				
			  				flag=false;
			  				leftmenu.style.transform='translate(0,0)';
			  				
			  				bg.style.display='block';
			  				
			  			}else{	
			  				
			  				flag=true;
			  				leftmenu.style.transform='translate(-100%,0)';
			  				
			  				bg.style.display='none';
			  			}
			  			
			  		
			  		
			  	    }



  	            },

  	            // 首页接受渲染数据
  	             requestData(){
  	             	var api=this.api+'api/productlist';
  	             	this.$http.jsonp(api).then(res=>{
  	             		console.log(res);
  	             		this.list=res.body.result;

  	             		


  	             	},err=>{
  	             		console.log(err);
  	             	});


  	             },
  	             scroll_top(key){

  	             	//var titleDom=document.querySelectorAll('.content_title');
  	             	//querySelectorAll和getElementsByTagName返回的都是伪数组
  	             	var titleDom=document.getElementsByTagName("h3");

  	             	//console.log(titleDom);
  	             	document.documentElement.scrollTop=titleDom[key].offsetTop;

  	             	var leftmenu=document.getElementById('left_menu');		  		
			  		var bg=document.getElementById('bg');	
			  		leftmenu.style.transform='translate(-100%,0)';
			  		bg.style.display='none';
			  		


  	             },
  	             // 接收购物车数量
  	             requestcartNum(){
  	             	var api=this.api+'api/cartCount?uid=a002';
  	             	this.$http.jsonp(api).then(res=>{
  	             		console.log(res);
  	             		this.cartnum=res.body.result;
  	             		
  	       

  	             	},err=>{
  	             		console.log(err);
  	             	});


  	             }

  

  },
 

  mounted(){
  	this.menuclick();
  	this.requestData();
  	this.requestcartNum();

  },
  components:{
    'v-nav':nav,

  }

}

  


</script>

<style lang="scss">
@charset "UTF-8";
.index_header{
	width:96%;
	height:6rem;
	margin:0 auto;
	background:#fff;
	display:flex;
	margin-top:1rem;
	border-radius:.8rem;
	.hlist{
		flex:1;
		border-right: 1px solid #eee;
        text-align:center;
		padding:.8rem;
		
		img{
			width:3rem;
			height:3rem;
		}
		p{
			margin-top:-.5rem;
		}

	}
}

.left_menu{
	transition: all 1s;
    transform: translate(-100%,0);
	width:6rem;
	height:100%;
	background:#eee;
	position:fixed;
	top:0;
	left:0;
	z-index: 2;
	
  
	ul{
		
		height:100%;
		position:relative;
		background:#eee;
		z-index:3;
		
		li{
			line-height:4.8rem;
			margin-left:.5rem;

		}

	}
	.nav_img{
		width:6rem;
		height:4rem;
		background: rgba(132, 128, 128, 0.9);
		position:relative;
		left:3.2rem;
		top:-62%;
		border-radius:1.8rem;
		z-index:2;
		img{
			width:1.8rem;
			height:1.8rem;
			margin-left:3rem;


		}
		p{
			text-align:center;
			color:#fff;
			margin-left:2rem;
			margin-top:-.4rem;
		}


	}
}


.content{


	.content_title{
		margin-top:1rem;
		text-align:center;
	}
	.item_list{
		display:flex;
		flex-wrap:wrap;
		//padding:1rem;
		li{
			width:33.33%;
			box-sizing: border-box;
			padding:.5rem;
			.inner{
				background:#fff;
				width:100%;
				border-radius:0.8rem;
				overflow:hidden;
				img{
					width:100%;
					height:8rem;

				}
				.pname{
					font-weight:bold;
					padding:.5rem;
				}
				.price{
					
					padding:.5rem;

				}
			}
		}

	}



}

/*背景图*/
.bg{
    
    position: fixed;
  
    width: 100%;
    height: 100%;
    
    background: rgba(132, 128, 128, 0.4);
    
    left: 0px;
    
    top:0px;
    z-index: 1;
    
    display: none;
    
    
}

</style>



