<template>
  <div id="p_content">
  	<router-link to="index">
    <div class="back" id="back"><span>返回</span></div>
    </router-link>
		
		<div id="p_content" class="p_content" >
			<img :src="api+list.img_url" alt="">
			<h3>{{list.title}}</h3>
			<p>{{list.price}}元/份</p>
		</div>
		<div id="p_info" class="p_info" >
			<h3>商品详情</h3>
			<img :src="api+list.img_url" alt="">
			<p v-html="list.content"></p>
			
		</div>

		<div class="p_footer" id="p_footer">
		
			<div class="p_num">
			    <span>数量：</span>
				<div class="input_left" @click="decNum()">-</div>
				<div class="input_center"><input type="text" v-model="num"></div>
				<div class="input_right" @click="incNum()">+</div>
			</div>
			
			<button class="cart" @click="addCart()">加入购物车</button>
		    
		</div>
  </div>
</template>

<script>
import config from "../assets/model/config.js"
export default {
 
  data () {
    return {
      msg: '',
      list:[],
      api:config.api,
      num:1
    }
  },
  methods:{
  	requestData(id){
  	    var api=this.api+'api/productcontent?id='+id;
  		this.$http.jsonp(api).then(res=>{
  			console.log(res);
  			this.list=res.body.result[0];

  		},err=>{
  			console.log(err);

  		});
  	},
  	addCart(){
  		 var api=this.api+'api/addcart'
  		this.$http.post(api,{

  		     uid:'a002',
             title:this.list.title,
             price:this.list.price,
             num:this.num,
             product_id:this.list._id,
             img_url:this.list.img_url


  		}).then(res=>{
  			console.log(res);
  			if(res.body.success){
  				this.$router.push({path:"/index"});
  			}

  			

  		},err=>{
  			console.log(err);

  		});
  	},
  	incNum(){
  		++this.num;

  	},
  	decNum(){
  		if(this.num>1){
  			--this.num;
  		}
  		

  	}

  	
   

  },
  mounted(){
  	// get传值接收参数
    var id=this.$route.query.id;
  	this.requestData(id);
  	//console.log(this.$route.query);
  
  }
 
  }
  

</script>

<style lang="scss">
@charset "utf-8";

.back{
	background: #000;
	width:4rem;
	height: 4rem;
	color: #fff;
	position: fixed;
	top:0.5rem;
	left: .5rem;
	border-radius: 50%;
    text-align: center;
    line-height: 2rem;
    padding: .2rem;
    span{
    	
    	margin:0  1.1rem;
    }
     


    &:before{
    	content:"";
    	display: block;
    	width:0.8rem;
    	height: .8rem;
    	border-left: .2rem solid #fff;
    	border-bottom: .2rem solid #fff;
    	position: relative;
    	top:1.6rem;
    	left: .2rem;
    	transform:rotate(45deg);
    	margin-right: 0.4rem;


    }

}
.p_content{
	background: #fff;
	border-radius: .8rem;
	img{
		width: 100%;
		height: 20rem;

	}
	h3{
		font-size: 1.6rem;
		padding: .5rem;
	}
	p{
		color: red;
		padding: .5rem;
	}
}

.p_info{
	background: #fff;
	padding: 1rem;
	margin-top: 1.2rem;
	h3{
		padding: .8rem 0;
	}

	img{
		max-width: 100%;
		display: block;
		margin:0 auto;
		
	}
	*{
		line-height: 2rem;
		color: #666;

	}

}

.p_footer{
	background:#fff;
	position: fixed;
	width: 100%;
	height: 4.2rem;
	bottom: 0;
	left: 0;
	border-top: 1px solid #eee;

	.p_num{
		display: flex;
		margin-top: .8rem;
		span{

			font-size: 1.6rem;
			padding:.2rem 0 0 0.2rem; 

		}
		.input_left,.input_right{

			font-size: 2.4rem;
		    width:3rem;
			height: 3rem;
			border:1px solid #eee;
			text-align: center;
			line-height: 3rem;
			color: red;
		}
		.input_center{


				input{
					width: 3rem;
					height: 3rem;
					border:1px solid #eee;
					text-align: center;
				}
				
			}

	}
	button{
		position: fixed;
		width: 8rem;
		height: 3.2rem;
		bottom: .1rem;
		right:0.5rem;
		background: red;
		color:#fff;
		border-radius: 0.8rem;
	}


}

</style>



